"use client";

//React and Next
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

//API
import {
  FailedOrderCountApi,
  FailedOrderListApi,
  invoicedownload,
  orderdetails,
  orderdetailsCount,
} from "@/api/clientsideaction/getfilter";

// 3rd Party
import { useRouter } from "nextjs-toploader/app";
//Redux
import { useDispatch, useSelector } from "react-redux";
import { offsetCount } from "@/redux/slices/userSlice";

//Components
import Breadcrumb from "../Breadcrumbs";
import Sidebar from "../Myprofile/sidebar";
import OpenedOrder from "./OpenedOrder";
import CancelOrder from "./CancelOrder";
import ClosedOrder from "./ClosedOrder";
import PaginationComp from "@/Components/Pagination";
import FailedOrderList from "./FailedOrder";
import Contactus from "../Contactus";

export default function OrderHistory({ token }) {
  const { t } = useTranslation();
  const offset = useSelector((state) => state.userReducer.offsetVal);
  const [state, setState] = useState(2);
  const [closed, setClosed] = useState([]);
  const [opened, setOpened] = useState([]);
  const [cancel, setCancel] = useState([]);
  const [failedOrderList, setFailedOrderList] = useState([]);
  const [loader, setLoader] = useState(false);
  const [search, setSearch] = useState("");
  const [triger, setTriger] = useState(0);
  // new
  const [orderCount, setOrderCount] = useState();
  // const off = searchparams.get('offset') ? searchparams.get('offset') : 0
  // const cleanedStr = off && off.replace(/[a-zA-Z]+/g, '') ? off.replace(/[a-zA-Z]+/g, '') : 0;
  // const [offset, setOffset] = useState(parseInt(cleanedStr))
  const [currentPage, setCurrentPage] = useState(1);

  let dispatch = useDispatch();

    const orderBreadCrumbEdit = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.orderHistory"),
      url: "javascript:void(0)",
    },
  ];
  useEffect(() => {
    setLoader(false);
    if (state == 4) {
      FailedOrderListApi(setFailedOrderList, setLoader, search, offset);
      FailedOrderCountApi(setOrderCount, setLoader, search);
    } else {
      orderdetailsCount(
        state == 1 ? "closed" : state == 2 ? "opened" : "cancelled",
        state == 1 ? setOrderCount : state == 2 ? setOrderCount : setOrderCount,
        setLoader,
        search
      );
      orderdetails(
        state == 1 ? "closed" : state == 2 ? "opened" : "cancelled",
        state == 1 ? setClosed : state == 2 ? setOpened : setCancel,
        setLoader,
        search,
        offset
      );
    }
  }, [triger, offset]);

  // pagenation api call
  const router = useRouter();
  const pagenationapi = async () => {
    if (state == 4) {
      FailedOrderListApi(setFailedOrderList, setLoader, search, offset);
      FailedOrderCountApi(setOrderCount, setLoader, search);
    } else {
      orderdetails(
        state == 1 ? "closed" : state == 2 ? "opened" : "cancelled",
        state == 1 ? setClosed : state == 2 ? setOpened : setCancel,
        setLoader,
        search,
        offset
      );
      orderdetailsCount(
        state == 1 ? "closed" : state == 2 ? "opened" : "cancelled",
        state == 1 ? setOrderCount : state == 2 ? setOrderCount : setOrderCount,
        setLoader,
        search
      );
    }
  };

  useEffect(() => {
    if (offset != 0) {
      pagenationapi();
    }
  }, [offset]);

  const onPageChange = (data) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setLoader(false);
    let limit = 5;
    let offset = Math.ceil((data - 1) * limit);
    // setOffset(offset)
    dispatch(offsetCount(offset));
    setCurrentPage(data);
    // router.push(`/account-settings/order-history?offset=${offset}`)
  };
  const nPages1 = Math.ceil(orderCount != undefined && orderCount / 5);
  useEffect(() => {
    setCurrentPage(offset / 5 + 1);
  }, []);

  const handleReset = () => {
    setLoader(false);
    if (state == 4) {
      FailedOrderListApi(setFailedOrderList, setLoader, search, offset);
      FailedOrderCountApi(setOrderCount, setLoader, search);
    } else {
      orderdetailsCount(
        state == 1 ? "closed" : state == 2 ? "opened" : "cancelled",
        state == 1 ? setOrderCount : state == 2 ? setOrderCount : setOrderCount,
        setLoader,
        ""
      );

      orderdetails(
        state == 1 ? "closed" : state == 2 ? "opened" : "cancelled",
        state == 1 ? setClosed : state == 2 ? setOpened : setCancel,
        setLoader,
        "",
        offset
      );
    }
    setCurrentPage(offset / 5 + 1);
    setSearch("");
  };

  const handleInvoice = (id) => {
    invoicedownload(token, id);
  };
  const handletabs = (id) => {
    // router.push(`/account-settings/order-history?offset=${0}`)
    dispatch(offsetCount(0));
    setState(id);
    setTriger(triger + 1);
  };
  const handleSearch = () => {
    setTriger(triger + 1);
    dispatch(offsetCount(0));
    // setOffset(0)
    setCurrentPage(1);
  };

  return (
    <>
      <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
        <Breadcrumb breadCrumb={orderBreadCrumbEdit} />

        <div className="flex rtl:space-x-[unset] md:space-x-8 mx-auto mt-4 mb-7">
          <Sidebar />
          <div className="w-full p-0 md:p-0 md:pe-8">
            <div className="flex justify-between items-center space-x-4 mb-[23px]">
              <h3 className="text-lg font-black text-black-300">{`${t("orderhistory.Order")}`}</h3>
              {state !== 4 && (
                <div className="flex items-center space-x-4 rtl:space-x-[unset]">
                  <div className="relative rtl:ml-4">
                    <input
                      type="text"
                      value={search}
                      placeholder={t("orderhistory.Search")}
                      onChange={(e) => setSearch(e.target.value)}
                      className="pr-20 border border-grey-400 rounded-md focus-visible:outline-0 w-full sm:min-w-[328px] h-10 text-black-200"
                    />
                    <button
                      className={`flex absolute top-0 right-0 btn  justify-center items-center px-3 h-10 bg-boldblack text-white text-base font-normal rounded-r-md hover:text-white ${
                        search == "" && "cursor-default pointer-events-none"
                      }`}
                      onClick={() => handleSearch()}
                    >
                      {t("orderhistory.Search")}
                    </button>
                  </div>
                  <button
                    className="flex items-center justify-center h-10 px-4 text-base font-normal text-black border border-black rounded hover:bg-slate-100 btn"
                    onClick={handleReset}
                  >
                    {t("orderhistory.Reset")}
                  </button>
                </div>
              )}
            </div>
            <div>
              <div className="flex items-center space-x-8 mb-[25px] border-slate-50 border-b overflow-auto whitespace-nowrap">
                <a
                  role="button"
                  className={`pb-1 font-semibold text-sm text-black-300 hover:font-bold rtl:ml-8  ${
                    state == 2
                      ? `pointer-events-none border-b-2 border-black font-active`
                      : "cursor-pointer"
                  }`}
                  onClick={() => {
                    handletabs(2);
                    setCurrentPage(1);
                    dispatch(offsetCount(0));
                  }}
                >
                  {t("orderhistory.OpnOrd")}
                </a>
                <a
                  role="button"
                  className={`pb-1 font-semibold text-sm text-black-300 hover:font-bold  ${
                    state == 1
                      ? "pointer-events-none border-b-2 border-black font-active"
                      : "cursor-pointer"
                  }`}
                  onClick={() => {
                    handletabs(1);
                    setCurrentPage(1);
                    dispatch(offsetCount(0));
                  }}
                >
                  {t("orderhistory.CldOrd")}
                </a>

                <a
                  role="button"
                  className={`pb-1 font-semibold text-sm text-black-300 hover:font-bold  ${
                    state == 3
                      ? "pointer-events-none border-b-2 border-black font-active"
                      : "cursor-pointer"
                  }`}
                  onClick={() => {
                    handletabs(3);
                    setCurrentPage(1);
                    dispatch(offsetCount(0));
                  }}
                >
                  {t("orderhistory.CnlOrd")}
                </a>
                <a
                  role="button"
                  className={`pb-1 font-semibold text-sm text-black-300 hover:font-bold  ${
                    state == 4
                      ? "pointer-events-none border-b-2 border-black font-active"
                      : "cursor-pointer"
                  }`}
                  onClick={() => {
                    handletabs(4);
                    setCurrentPage(1);
                    dispatch(offsetCount(0));
                  }}
                >
                  {t("orderhistory.failOrd")}
                </a>
              </div>
              {state == 1 && (
                <ClosedOrder
                  closed={closed}
                  loader={loader}
                  handleInvoice={handleInvoice}
                />
              )}
              {state == 2 && (
                <OpenedOrder
                  opened={opened}
                  loader={loader}
                  handleInvoice={handleInvoice}
                />
              )}
              {state == 3 && <CancelOrder cancel={cancel} loader={loader} />}
              {state == 4 && (
                <FailedOrderList
                  failedOrderList={failedOrderList}
                  loader={loader}
                />
              )}
              <div className="relative flex flex-col w-full mt-4 sm:flex-row">
                {orderCount > 5 && (
                  <PaginationComp
                    nPages1={nPages1}
                    currentPage={currentPage}
                    onPageChange={onPageChange}
                  />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>

      <Contactus token={token} />
    </>
  );
}
